import React from "react";
import {Box, Button, CardMedia, Typography} from "@mui/material";
import GradientContainer from "../theme/GradientContainer";
import MailListSignUpComponent from "../components/MailListSignUpComponent";

const Home: React.FC = () => {
    return (
        <>
            <GradientContainer>
                <Box
                    sx={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        minHeight: "100vh",
                        gap: "4rem",
                        padding: {xs: "5rem", md: "15rem"},
                        flexWrap: "wrap", // Ensures proper layout on smaller screens
                        textAlign: "center",
                    }}
                >
                    {/* Text Section */}
                    <Box
                        sx={{
                            flex: "1 1 auto",
                            maxWidth: "600px", // Constrains text width for better readability
                            padding: {xs: "1rem", sm: "2rem"},
                        }}
                    >
                        <Typography
                            variant="h1"
                            color="inherit"
                            sx={{
                                fontSize: {xs: "2rem", sm: "3rem", md: "4rem", lg: "5rem"},
                                lineHeight: "1.2",
                                marginBottom: "1rem",
                            }}
                        >
                            ProjectsIncluded
                        </Typography>
                        <Typography
                            variant="h4"
                            color="inherit"
                            sx={{
                                fontSize: {xs: "1.2rem", sm: "1.5rem", md: "2rem"},
                                fontStyle: "italic",
                                marginTop: "1rem",
                                wordBreak: "break-word",
                                maxWidth: "100%",
                            }}
                        >
                            "Elevate your applications with seamless scalability"
                        </Typography>
                    </Box>

                    {/* Image Section */}
                    <CardMedia
                        component="img"
                        image="cloud_server_secure.png"
                        alt="Secure Cloud Server"
                        sx={{
                            flex: "0 1 auto",
                            width: {xs: "80%", sm: "60%", md: "50%", lg: "40%"},
                            maxWidth: "400px", // Limits the max size of the image
                            height: "auto",
                        }}
                    />
                    <MailListSignUpComponent/>
                </Box>
            </GradientContainer>
        </>
    );
};

export default Home;
